  selectMyGroupAchiInfo(groupType) {
    // 我的客户群信息查询
    let params = {
      ownerId: this.userId,
      groupType: groupType
    }
    if (groupType == 1) {

      this.httpService.selectMyGroupAchiInfo(params).subscribe(data => {
        if (data.returnCode.code == 'success') {
          this.groupOwnerInfoBean = data;
          let alLegendData = [];
          let alSeriesData = [];
          let alSeriesData2 = [];
          if (data.achiInfo) {
            data.achiInfo.forEach(item => {
              alLegendData.push(item.groupName);
              // alSeriesData.push({value:item.mdeptAvgBal,name:item.groupName});
              alSeriesData2.push({ value: item.mloanAvgBal, name: item.groupName });
            });

          }
          this.option_lnList = {
            title: {
              text: "贷款日均占比分布（单位：万元）",
              subtext: "",
              x: "center"
            },
            tooltip: {
              trigger: 'item',
              formatter: "{a}<br/>{b}:{c} ({d}%)"
            },
            legend: {
              orient: "vertical",
              left: 'left',
              data: alLegendData,
            },
            series: [
              {
                name: "贷款占比",
                type: "pie",
                radius: "70%",
                center: ["50%", "60%"],
                data: alSeriesData2,
                itemStyle: {
                  emphasis: {
                    shadowBlur: 10,
                    shadowOffsetX: 0,
                    shadowColor: 'rgba(0,0,0,0.5)'
                  }
                }
              }
            ]
          };
        } else {
          this.msgs = [];
          data.returnCode.code ? data.returnCode.code : '调用服务失败！';
          this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
        }
      }, err => {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
      })
    } else {

      this.httpService.selectMyGroupAchiInfo(params).subscribe(data => {
        if (data.returnCode.code == 'success') {
          this.groupOwnerInfoBean = data;
          let alLegendData = [];
          let alSeriesData = [];
          let alSeriesData2 = [];
          if (data.achiInfo) {
            data.achiInfo.forEach(item => {
              alLegendData.push(item.groupName);
              alSeriesData.push({ value: item.mdeptAvgBal, name: item.groupName });
              // alSeriesData2.push({value:item.mloanAvgBal,name:item.groupName});
            });

          }
          this.option_asList = {
            labels: alLegendData,
            title: {
              text: "存款日均占比分布（单位：万元）",
              subtext: "",
              x: "center"
            },
            tooltip: {
              trigger: 'item',
              formatter: "{a}<br/>{b}:{c} ({d}%)"
            },
            legend: {
              orient: "vertical",
              left: 'left',
              data: alLegendData,
            },
            series: [
              {
                name: "存款占比",
                type: "pie",
                radius: "70%",
                center: ["50%", "60%"],
                data: alSeriesData,
                itemStyle: {
                  emphasis: {
                    shadowBlur: 10,
                    shadowOffsetX: 0,
                    shadowColor: 'rgba(0,0,0,0.5)'
                  }
                }
              }
            ]
          };
        } else {
          this.msgs = [];
          data.returnCode.code ? data.returnCode.code : '调用服务失败！';
          this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
        }
      }, err => {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
      })
    }
  }
