  //模板码值
  templeteChange(event, index, i, ind) {
    // console.log(event);
    let arr = 'CusGroupAttrOpt' + index;//下一个下拉菜单的信息
    //清空已选择的值
    if (index == 2) {
      this.conditionList[i][ind]['fieldAttr'] = '';
      this.conditionList[i][ind]['field'] = '';
      this.conditionList[i][ind]['operator'] = '';
      this.conditionList[i][ind]['value'] = '';
      this.conditionList[i][ind]['label'] = '';
    } else if (index == 3) {
      this.conditionList[i][ind]['operator'] = '';
      this.conditionList[i][ind]['value'] = '';
      this.conditionList[i][ind]['label'] = '';
    }

    // console.log(this.conditionList);
    let taskTypeParentId;
    if (index == 4) {
      taskTypeParentId = index == '1' ? '' : event.value[0];
    } else {
      taskTypeParentId = index == '1' ? '' : event.value;
    }
    // if (index == 1) {
    //   this.CusGroupAttrOpt2 = [];
    //   this.CusGroupAttrOpt3 = [];
    // } else if (index == 2) {
    //   this.CusGroupAttrOpt3 = [];
    // }
    let params = {
      taskTypeKey: 'groupType',
      taskTypeParentId: taskTypeParentId
    }
    // console.log('0000000000')
    // console.log(this.conditionList);
    //初始化 给群状态默认
    // this.conditionList[0][0].fieldModule = "001";
    // this.templeteChange2();

    this.httpService.taskTypeQry(params).subscribe(data => {
      if (data.returnCode.code == 'success') {
        // console.log(data)
        // console.log(data.taskTypeList);
        if (data.taskTypeList && data.taskTypeList.length != 0) {
          if (index == 3) {
            //三级
            this.conditionList[i][ind][arr] = [{
              label: '请选择', value: ''
            }];
            data.taskTypeList.forEach(item => {
              this.conditionList[i][ind][arr].push({
                label: item.taskTypeName,
                value: [item.createrName, item.taskTypeLevel],
              });
            });

            if (data.taskTypeList.length == 1) {//(三级唯一时默认选择)
              this.conditionList[i][ind]['field'] = [data.taskTypeList[0].createrName, data.taskTypeList[0].taskTypeLevel];
              this.templeteChange({ value: [data.taskTypeList[0].createrName, data.taskTypeList[0].taskTypeLevel] }, 4, i, ind);
            }
          } else {
            //二级 一级
            this.conditionList[i][ind][arr] = [{
              label: '请选择', value: ''
            }];
            data.taskTypeList.forEach(item => {
              this.conditionList[i][ind][arr].push({
                label: item.taskTypeName,
                value: item.taskTypeId,
              });
            });
          }
        } else {
          //四级
          switch (event.value[1]) {
            case '0':
              this.conditionList[i][ind]['valueType'] = this.isCountType;
              this.conditionList[i][ind]['special'] = 'count';
              break;
            case '1':
              this.conditionList[i][ind]['valueType'] = this.CountTypeLike;
              this.conditionList[i][ind]['special'] = 'count';
              this.conditionList[i][ind]['operator'] = '%%';
              break;
            case '00':
              this.conditionList[i][ind]['valueType'] = this.CountTypeEqual;
              this.conditionList[i][ind]['special'] = 'count';
              this.conditionList[i][ind]['operator'] = '=';
              break;
            case '01':
              this.conditionList[i][ind]['valueType'] = this.CountTypeEqual;
              this.conditionList[i][ind]['special'] = 'code';
              this.conditionList[i][ind]['specialType'] = this.groupType;//群类型
              this.conditionList[i][ind]['operator'] = '=';
              break;
            case '02':
              this.conditionList[i][ind]['valueType'] = this.CountTypeEqual;
              this.conditionList[i][ind]['special'] = 'code';
              this.conditionList[i][ind]['specialType'] = this.developmentLevel;//开发程度
              this.conditionList[i][ind]['operator'] = '=';
              break;
            case '03':
              this.conditionList[i][ind]['valueType'] = this.CountTypeEqual;
              this.conditionList[i][ind]['special'] = 'code';
              this.conditionList[i][ind]['specialType'] = this.productList;//主推产品
              this.conditionList[i][ind]['operator'] = '=';
              break;
            case '2':
              this.conditionList[i][ind]['valueType'] = this.CountTypeEqual;
              this.conditionList[i][ind]['special'] = 'input';
              this.conditionList[i][ind]['inputType'] = 'bank-teller';
              this.conditionList[i][ind]['operator'] = '=';
              break;
            case '3':
              this.conditionList[i][ind]['valueType'] = this.CountTypeLike;
              this.conditionList[i][ind]['special'] = 'input';
              this.conditionList[i][ind]['inputType'] = 'bank-teller';
              this.conditionList[i][ind]['operator'] = '%%';
              break;
            case '4':
              this.conditionList[i][ind]['valueType'] = this.CountTypeLike;
              this.conditionList[i][ind]['special'] = 'input';
              this.conditionList[i][ind]['inputType'] = 'bank-cust';
              this.conditionList[i][ind]['operator'] = '%%';
              break;
            case '5':
              this.conditionList[i][ind]['valueType'] = this.InTypeEqual;
              this.conditionList[i][ind]['special'] = 'input';
              this.conditionList[i][ind]['inputType'] = 'bank-org';
              this.conditionList[i][ind]['operator'] = 'in';
              break;
            case '04':
              this.conditionList[i][ind]['valueType'] = this.CountTypeEqual;
              this.conditionList[i][ind]['special'] = 'code';
              this.conditionList[i][ind]['specialType'] = this.GroupStatus;//群状态
              this.conditionList[i][ind]['operator'] = '=';
              break;
            case '05':
              this.conditionList[i][ind]['valueType'] = this.isCountType;
              this.conditionList[i][ind]['special'] = 'time';
              break;
            default:
              this.conditionList[i][ind]['valueType'] = [];
              this.conditionList[i][ind]['special'] = 'count';
              break;
          }
        }
      }
      else {
        this.msgs = [];
        data.returnCode.message ? data.returnCode.message : '查询失败！';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
    })
  }

