  //搜索条件
  searchCondition(index) {
    let list = [];
    if (index == 1 || index == 3) {
      if (this.conditionList) {
        this.conditionList.forEach((element, i) => {
          let arr = [];
          let valueTemp;
          if (element) {
            element.forEach((element2, j) => {
              //创建日期的转换
              if (element2.field[0] == 'create_time') {
                if (typeof (element2.value) != 'string') {
                  valueTemp = this.commfunc.transDateN(element2.value);
                }
              } else {
                valueTemp = element2.value;
              }
              //群联系人
              if (element2.field[0] == "group_of_contacts") {
                valueTemp = "," + element2.value;
              }
              //条件为空,默认传空
              if (element2.field == '') {
                element2.field = [''];
              }
              arr.push({
                field: element2.field[0],
                operator: element2.operator,
                value: valueTemp,
              })
            });
          }
          // console.log(arr);
          if (arr.length > 0) {
            list.push(arr)
          }
        });
      }
    }
    if (list.length != 0) {
      let arrTemp = [];
      list.forEach(element => {
        arrTemp.push(element);
      });
      list = arrTemp;
    } else {
      list = null;
    }
    return list
  }

  //组合方式(搜索条件)
  compoundMode(list) {
    let searchInput = '';
    if (this.searchContent == '') {
      if (list.length > 1) {
        list.forEach((element, i) => {
          if ((i + 1) != list.length) {
            searchInput = searchInput + (i) + '且';
          } else {
            searchInput = searchInput + (i);
          }
        });
      } else {
        searchInput = '0';
      }
    } else {
      let searchContent = this.searchContent.replace(/10/g, '0');
      let arrTemp = searchContent.split('');
      let reg = /^[1-9]$/;
      arrTemp.forEach((element, i) => {
        if (element == '0') {
          arrTemp[i] = '9';
        } else {
          if (reg.test(element)) {
            arrTemp[i] = (parseInt(arrTemp[i]) - 1).toString();
          }
        }
      });
      searchInput = arrTemp.join('');
    }
    return searchInput
  }

