  //客户群列表信息 查询
  tplSearch(index) {
    if (index == 1) {// 页面初始化
      //主管群
      this.pageNum1 = 1;
      this.pageSize1 = 10;
      this.first1 = 0;
      //辅管群
      this.pageNum2 = 1;
      this.pageSize2 = 10;
      this.first2 = 0;
    }

    let list = this.searchCondition(index);//搜索条件
    let searchInput = this.compoundMode(list);//组合方式

    //判断是否主管群
    let isAssist = null;
    let pageNum = this.pageNum1;
    let pageSize = this.pageSize1;
    let attr = 'groupList1';
    if (!this.isMain) {
      pageNum = this.pageNum2;
      pageSize = this.pageSize2;
      isAssist = true;
      attr = 'groupList2';
    }

    let dataDt;//查询历史记录
    if (this.isMain) {
      if (this.chooseMonthMain && this.isMainHistory) {
        dataDt = this.chooseMonthMain;
      } else {
        dataDt = undefined;
      }
    } else {
      if (this.chooseMonthNotMain && this.notMainHistory) {
        dataDt = this.chooseMonthNotMain;
      } else {
        dataDt = undefined;
      }
    }

    let sum;//汇总数据
    if (this.total == true) {
      sum = '1';
    }

    let queryTypeFlag;
    if (this.selcetedValues1 || this.selcetedValues2) {
      queryTypeFlag = this.queryTypeFlag;
    }

    let params = {
      pageNum: pageNum,
      pageSize: pageSize,
      conditionList: list,
      isAssist: isAssist,
      dataDt: dataDt,
      groupType: '1', //区分客户群和自选群
      advanceInput: searchInput,
      isCount: sum,
      queryTypeFlag: queryTypeFlag
    }
    this.downList = list;
    this.downisAssist = isAssist;
    this.downdataDt = dataDt;

    console.log(this.conditionList);
    this.httpService.queryCustGroupInfo(params).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this[attr] = data.resultList;
        if (!this.isMain) {
          this.pageNum2 = data.pageNo;
          this.pageSize2 = data.pageSize;
          this.totalNum2 = data.totalNum;
        } else {
          this.pageNum1 = data.pageNo;
          this.pageSize1 = data.pageSize;
          this.totalNum1 = data.totalNum;
        }
        this[attr].forEach((element, i) => {
          if (element.group_type == '') {
            this[attr][i].group_type = '';
          } else {
            this.groupType.forEach(element_2 => {//群类型
              if (element_2.value == element.group_type) {
                this[attr][i].group_type = element_2.label;
              }
            });
          }
          if (element.featured_products == '') {
            this[attr][i].featured_products = '';
          } else {
            this.productList.forEach(element_2 => {//主推产品
              if (element_2.value == element.featured_products) {
                this[attr][i].featured_products = element_2.label;
              }
            });
          }
          if (element.dev_degree == '') {
            this[attr][i].dev_degree = '';
          } else {
            this.developmentLevel.forEach(element_2 => {//开发程度
              if (element_2.value == element.dev_degree) {
                this[attr][i].dev_degree = element_2.label;
              }
            });
          }
          if (element.status_id == '') {
            this[attr][i].status_id = '';
          } else {
            this.GroupStatus.forEach(element_2 => {//群状态
              if (element_2.value == element.status_id) {
                this[attr][i].status_id = element_2.label;
              }
            });
          }
          if (element.create_time != null && element.create_time != '') {//创建日期
            element.create_time = element.create_time.split(' ')[0];
          }
          if (element.status_id == '') {
            this[attr][i].status_id = '';
          } else {
            this.GroupStatus.forEach(element_2 => {//群状态
              if (element_2.value == element.status_id) {
                this[attr][i].status_id = element_2.label;
              }
            });
          }
        });

        //汇总数据(拼接在列表最下一行)
        if (data.resultSumList && data.resultSumList.length != 0) {
          this[attr].push(data.resultSumList[0]);
        }

        this.moneyList.forEach(element => {//主管群
          if (this[attr] && this[attr].length != '') {
            this[attr].forEach((element2, i) => {
              for (let k in element2) {
                if (element == k) {
                  this[attr][i][k] = this.formatMoneyValuePie.transform(element2[k]);
                }
              }
              if (!element2['group_id']) {//合计
                element2['sumFlag'] = true;
              } else {
                element2['sumFlag'] = false;
              }
            });
          }
        });
        debugger
        console.log(this[attr]);
      } else {
        if (!data.errorMessage) {
          this.msgs = [];
          data.returnCode.message ? data.returnCode.message : '查询失败！';
          this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
        } else {
          this.msgs = [];
          this.msgs = [{ severity: 'error', summary: '提示', detail: '高级筛选条件格式输入错误' }];
        }
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
    })
  }

