  toDownload() {
    // console.log(this.headerList);
    this.titleList = [{ taskTypeName: '群编号', createrName: 'group_id' }];

    this.headerList.forEach(element => {
      let arr = {
        taskTypeName: element.taskTypeName,
        createrName: element.createrName
      }
      this.titleList.push(arr);
    });

    let searchInput = this.compoundMode(this.downList);//组合方式

    let params = {
      listShowsIdType: '1',
      listShowsIdModule: '01',
      createOrg: this.orgId,
      createdByUserLogin: this.userId,
      conditionList: this.downList,
      isAssist: this.downisAssist,
      dataDt: this.downdataDt,
      groupType: '1',
      advanceInput: searchInput,
      titleList: this.titleList
    }
    this.httpService.queryCustGroupInfoExportFile(params).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '下载提示', detail: "提交成功！" });
        this.router.navigate(['pages/tzb/custom/downloadCenter/downCenter']);
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '下载提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '下载提示', detail: "调用服务失败！" });
    })
  }
  outDisplayCall(event) {
    this.downloadPerson = event;
  }
  outValueCall(event) {
    this.outVal = event;
    let ss = '';
    this.outVal.forEach(item => {
      ss += item.tellerId + ','
    });
    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    commonHeader.extParam['specifyNextOpers'] = ss;
    this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
    this.toDownload();
  }
