  //用户列表登记
  addUserListShow() {
    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    let chName = JSON.parse(this.commfunc.getSessionDataCH('chName'));
    let orgID = commonHeader.orgId;
    let userId = commonHeader.userId;
    let arr = [];
    let arr2 = [];
    let arrTemp = [];
    this.getSecondCustList.forEach(element => {
      element.forEach(element2 => {
        arrTemp.push(element2);
      });
    });

    this.selectedHeaderList.forEach(element => {//
      arrTemp.forEach(element_2 => {
        if (element_2.taskTypeName == element) {
          arr2.push(element_2);
        }
      });
    });

    arr2.forEach(element => {
      this.getHeaderList.forEach(element_2 => {
        if (element_2.taskTypeParentId == element.taskTypeId) {
          arr.push(element_2);
        }
      })
    });

    if (arr.length > 50) {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '所选字段不能多于50个' }];
      return false;
    }

    let params = {
      listShowsIdType: '1',
      listShowsIdModule: '01',
      createOrg: orgID,
      createdByUserLogin: userId,
      exploreConditionObject: JSON.stringify(arr)
    }
    this.httpService.addUserListShow(params).subscribe(data => {
      if (data.returnCode.code == 'success') {
        // console.log(data);
        this.attrListSearch();
      }
      else {
        this.msgs = [];
        data.returnCode.message ? data.returnCode.message : '查询失败！';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
    })
  }

