  //列表查询`````````````````````````````````````````````````````````````````````````````````````````````
  attrListSearch() {
    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    let chName = JSON.parse(this.commfunc.getSessionDataCH('chName'));
    let orgID = commonHeader.orgId;
    let userId = commonHeader.userId;
    let params = {
      listShowsIdType: '1',
      listShowsIdModule: '01',
      createOrg: orgID,
      createdByUserLogin: userId
    }
    this.httpService.selectUserListShow(params).subscribe(data => {
      if (data.returnCode.code == 'success') {
        // console.log(this.getHeaderList);
        this.moneyList = [];
        if (data.exploreConditionObject && data.exploreConditionObject != '') {
          let eco = JSON.parse(data.exploreConditionObject);
          eco = eco.filter((item)=>{
            return item.createrName != 'group_id';
          })
          eco.forEach((element, i) => {
            if (element.updater == '1') {
              this.moneyList.push(element.createrName);
            }
            if (element.createrName == 'group_name' || element.createrName == 'address_message' || element.createrName == 'group_of_contacts' || element.createrName == 'group_of_evaluation' || element.createrName == 'org_id' || element.createrName == 'create_org_id') {
              element.width = '250px';
            } else {
              element.width = '120px';
            }
          });
          this.headerList = eco;
          console.log(this.headerList);
          console.log(this.moneyList);

          // this.moneyList.forEach(element => {
          //   if (this.groupList1 && this.groupList1.length != '') {
          //     this.groupList1.forEach((element2, i) => {
          //       for (let k in element2) {
          //         if (element == k) {
          //           this.groupList1[i][k] = this.formatMoneyValuePie.transform(element2[k]);
          //         }
          //       }
          //     });
          //   }
          // });
        } else {//后台默认会存群编号(没有必要)
          this.headerList = [];
          this.getHeaderList.forEach(element => {
            let arr = ["群名称", "群类型", "群地址", "客户名单总数", "有效客户数", "存款有效客户数", "存款余额", "贷款余额", "群主", "群联系人"];
            arr.forEach(item => {
              if (element.taskTypeName == item) {
                this.headerList.push(element);
              }
            });
          });
          this.moneyList = ['存款余额', '贷款余额'];
        }
        // console.log(this.headerList)
      }
      else {
        this.msgs = [];
        data.returnCode.message ? data.returnCode.message : '查询失败！';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
    })

    if (!this.isFirst) {//非初始化
      this.tplSearch(3);
    }
  }

