  //映射模板
  mappedTemplete(col, isDialog) {
    if (isDialog == 2) {
      this.templeteManagerDisplay = false;
    }
    let expCondId = col.screenTemplateId ? col.screenTemplateId : '';
    let params = {
      requestType: '4',
      expCondId: expCondId,
    }
    //模板详情
    this.httpService.queryExploreConditionDetail(params).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.conditionList = data.requestMap.conditionList;
        this.conditionList.forEach((element, i) => {
          if (element && element.length > 0) {
            element.forEach((element_2, j) => {
              if (element_2.field[0] == "create_time") {
                this.conditionList[i][j]['value'] = new Date(this.conditionList[i][j]['value']);
              }
            });
          }
        });
        this.searchContent = data.requestMap.advanceInput;
      }
      else {
        this.msgs = [];
        data.returnCode.message ? data.returnCode.message : '查询失败！';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
    })
  }
  //模板删除
  deleteTarget(e) {
    if (e.createUserId == this.userId) {
      this.confirmationService.confirm({
        message: '是否确认删除?',
        header: '删除',
        icon: 'fa fa-question-circle',
        accept: () => {
          let param = { expCondId: e.screenTemplateId, requestType: '4' }
          this.httpService.deleteTargetCustomerExplore(param).subscribe((data) => {
            if (data.returnCode.code === "success") {
              this.msgs = [];
              this.msgs.push({ severity: 'success', summary: '查询列表提示', detail: data.returnCode.message });
              this.queryTargetCustomerExploreList(0);
            } else {
              this.msgs = [];
              this.msgs.push({ severity: 'error', summary: '查询列表提示', detail: data.returnCode.message });
            }
          }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '查询列表提示', detail: '调用服务失败！' });
          });
        }
      });
    }
    else {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '删除失败', detail: '不能删除他人模板' });
    }
  }
